.background{
    height:100%;
    width:100%;
    padding:none;
    position:fixed;
    object-fit:cover;
    z-index:-1;
    float: left;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}

@keyframes fadeIn {
    from {opacity:0}
    to{opacity:1}
}

.fade-in{
    animation:fadeIn 500ms ease-in-out;
}